body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  -webkit-overflow-scrolling: touch;
  overflow-y: scroll;
  overflow-x: hidden;
  height: 100vh;
  width: 100vw;

  /* padding added to prevent left and right rubberbanding */
  padding-right: .1rem; 
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.header {
    display: grid;
    grid-template-columns: repeat(10, 10%);
    padding: 1rem 2rem;
    /* background-color: #5a2b81; */
    background-color: black;
    color: white;
    overflow: hidden;
    margin: 0;
}

.header-logo-wrapper {
    grid-column-start: 1;
    grid-column-end: 2;
    display: flex;
    align-items: center;
    justify-content: center;
}

.header-logo {
    height: 4rem;
    border-radius: 50%;
    background-color: white;
}

.header-link {
    padding: .5rem;
}

.header-link:hover {
    cursor: pointer;
    animation-name: link-animation;
    animation-duration: .25s;
    background-color: #5a2b81;
    border: 2px solid white;
}

@keyframes link-animation {
    from {
        background-color: black;
        border: 2px solid black;
    }
    to {
        background-color: #5a2b81;
        border: 2px solid white;
    }
}

.header-link:hover:active {
    background-color: white;
    color: black;
    border: 2px solid white;
}

.header-news {
    grid-column-start: 7;
    grid-column-end: 8;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
}

.header-about {
    grid-column-start: 8;
    grid-column-end: 9;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
}

.header-data {
    grid-column-start: 9;
    grid-column-end: 10;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
}

.stats {
    padding: 3rem;
    overflow: auto;
    border: 1px solid black;
}

.stats thead tr th {
    padding: 0 .5rem;
}

.stats tbody tr td {
    padding: 0 .5rem;
    border: 1px solid black;
}

@media(max-width: 1024px) {
    .header {
        display: grid;
        grid-template-columns: repeat(4, 25%);
        padding: 1rem .5rem;
    }

    .header-news {
        grid-column-start: 2;
        grid-column-end: 3;
    }

    .header-about {
        grid-column-start: 3;
        grid-column-end: 4;
    }

    .header-data {
        grid-column-start: 4;
        grid-column-end: 5;
    }

    .stats {
        padding: 1rem;
    }
}